body {
  padding: 20px;
}

.container{
  width: 1290px;
  margin: 0 auto;
}
.banner img{
  width: 1480px;
  object-fit: cover;
}
/* Main section styles */
.new-products-section {
  max-width: 1200px;
  margin: 0 auto;
}

/* Row layout for text and products */
.products-row {
  display: flex;
  align-items: center;
  gap: 40px;
  margin-bottom: 40px;
}

/* Text content styles */
.text-content {
  flex: 0 0 300px;
}

.section-title {
  color: #2E8B2C;
  font-size: 36px;
  margin-bottom: 20px;
  font-weight: bold;
}

.section-description {
  color: #333;
  font-size: 16px;
  line-height: 1.5;
}

/* Product slider styles */
.product-slider-container {
  flex: 1;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none; /* Firefox */
}

.product-slider-container::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Edge */
}

.product-slider {
  display: flex;
  gap: 20px;
  padding-bottom: 10px;
}

/* Product card styles */
.product-card {
  min-width: 280px;
  border: 1px solid #EBEBEB;
  border-radius: 10px;
  padding: 15px;
  position: relative;
  background: white;
  transition: transform 0.3s, box-shadow 0.3s;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
}

.sale-badge {
  position: absolute;
  top: 15px;
  left: 15px;
  background-color: #2E8B2C;
  color: white;
  padding: 5px 15px;
  border-radius: 5px;
  font-weight: bold;
  z-index: 2;
}

.product-image {
  width: 100%;
  height: 250px;
  background-color: #F0F0F0;
  border-radius: 5px;
  margin-bottom: 15px;
  object-fit: cover;
}

.product-title {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
  min-height: 40px;
}

.product-price {
  font-size: 18px;
  font-weight: bold;
  color: #8B5A2B;
}

/* Features section */
.features-section {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 30px;
  margin-bottom: 30px;
}

.feature-card {
  flex: 1;
  width: 250px;
  border: 1px solid #EBEBEB;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  margin-bottom: 15px;
}

.feature-title {
  color: #2E8B2C;
  font-size: 18px;
  font-weight: bold;
}

.feature-description {
  color: #8B5A2B;
  font-size: 14px;
}

.hero-section-rela{
  position: relative;
}

.hero-section {
  width: 100%;
  min-height: 600px;
  background-image: url(/image/backrout1.png) ;
  background-size: cover;
  background-position: center;
  position: relative;
  overflow: hidden;
  padding: 40px;
  display: flex;
  align-items: center;
  margin-bottom: 100px;
}

.hero-content {
  width: 50%;
  position: relative;
  z-index: 2;
  padding-left: 40px;
}

.hero-title {
  color: white;
  font-size: 48px;    
  line-height: 1.2;
  margin-bottom: 30px;
  max-width: 500px;
}

.cta-button {
  display: inline-block;
  background-color: #F7A5A5;
  color: white;
  padding: 15px 40px;
  border-radius: 50px;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  transition: background-color 0.3s;
}

.cta-button:hover {
  background-color: #E88A8A;
}

.peach-product {
  position: absolute;
  right: 10%;
  top: 50%;
  transform: translateY(-50%);
  max-width: 300px;
  z-index: 1;
}

.category-nav {
  width: 100%;
  display: flex;
  gap: 20px;
  margin-top: 40px;
  flex-wrap: wrap;
  padding: 0 40px;
  position: absolute;
  bottom: -75px;
  left: 0;
  justify-content: center;
}

.category-item {
  background-color: #E6E6E6;
  opacity: 0.8;
  border-radius: 15px;
  padding: 10px;
  width: 130px;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
  text-decoration: none;
  color: #333;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}

.category-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
}

.category-icon {
  width: 60px;
  height: 60px;
  margin: 0 auto 15px;
  object-fit: contain;
}

.category-label {
  font-size: 14px;
  font-weight: bold;
}

.botanical-gallery {
  max-width: 1200px;
  margin: 60px auto;
  display: flex;
  gap: 30px;
  align-items: center;
}

/* Image frame styling */
.botanical-frame-right {
  
  margin-top: 100px;
  flex: 1;
  position: relative;
  overflow: hidden;
}

.botanical-frame-left {
  
  flex: 1;
  position: relative;
  overflow: hidden;
}

.botanical-photo {
  height: 800px;
  width: 600px;
  display: block;
  transition: transform 0.5s;
}

.botanical-photo:hover {
  transform: scale(1.03);
}

/* Caption styling */
.botanical-frame-left .botanical-caption {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 85px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 16px;
  position: absolute;
  top: 100px;
  text-align: center;
}

.botanical-frame-right .botanical-caption {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 65px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 16px;
  position: absolute;
  bottom: 100px;
  text-align: center;
}

.best-selling-products {
  max-width: 1200px;
  margin: 0 auto;
}

.best-selling-section-title {
  color: #2a7e19;
  font-size: 24px;
  margin-bottom: 20px;
  font-weight: bold;
}

.best-selling-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.best-selling-card {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  overflow: hidden;
  transition: transform 0.3s ease;
  position: relative;
}

.best-selling-card:hover {
  transform: scale(1.05);
}

.best-selling-badge {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: #2a7e19;
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: bold;
}

.best-selling-image {
  width: 100%;
  height: 250px;
  background-color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
}

.best-selling-details {
  padding: 15px;
}

.best-selling-name {
  font-size: 16px;
  margin-bottom: 10px;
  color: #333;
}

.best-selling-price {
  font-size: 18px;
  font-weight: bold;
  color: #2a7e19;
}

.best-selling-image img {
  width: 80%;
  height: 200px;
  background-color: #e0e0e0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.brand-value-section {
  margin-top: 50px;
  position: relative;
  display: flex;
  align-items: center;
  height: 600px;
  overflow: hidden;
  justify-content: center;
  align-items: center;
}

.brand-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}

.brand-content {
  position: absolute;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 40px;
  border-radius: 15px;
  max-width: 500px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
  text-align: center;
}

.brand-title {
  color: #2a7e19;
  font-size: 28px;
  margin-bottom: 20px;
  font-weight: bold;
}

.brand-description {
  color: #333;
  line-height: 1.6;
  margin-bottom: 25px;
}

.brand-cta {
  display: inline-block;
  background-color: #2a7e19;
  color: white;
  margin-left: 200px;
  padding: 12px 25px;
  text-decoration: none;
  border-radius: 25px;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.brand-cta:hover {
  background-color: #1e5612;
}
